<template>
  <div
    id="main"
    class="w-100 h-100">
    <header
      :class="{ 'scrolled' : y > 10 }"
      class="pa4">
      <h1 class="ma0">daetal.us</h1>
      <div class="shared">
        <ul>
          <li><a
            class="github"
            href="https://github.com/daetal-us"
            title="Code by Jon Adams as daetal-us on Github">Github</a></li>
          <li><a
            class="twitter"
            href="https://twitter.com/@daetalus"
            title="Links and other brief announcements by Jon Adams as @daetalus on Twitter">Twitter</a></li>
          <li><a
            class="medium"
            href="https://medium.com/@daetalus"
            title="Articles by Jon Adams as @daetalus on Medium">Medium</a></li>
          <li><a
            class="linkedin"
            href="https://linkedin.com/in/daetalus"
            title="Jon Adams as daetalus on LinkedIn">LinkedIn</a></li>
        </ul>
      </div>
    </header>
    <CyberSpace
      :message="text"
      class="db w-100 h-100" />
    <Search
      class="section search center f3 tc pv4 dn db-l"
      @modified="updatedText" />
    <div class="section works cf pa1 pa4-m pa4-l">
      <h2 class="title tc f3">public works</h2>
      <div class="card-slot fl w-100 w-25-l w-50-m pa4">
        <div class="card">
          <a
            class="image"
            href="http://foodvib.es">
            <img
              class="scale"
              alt="Food Vibes the book"
              src="static/media/food-vibes.jpg">
          </a>
          <a
            class="title"
            href="http://foodvib.es"><h3>Food Vibes</h3></a>
          <p>Casual Cookbook for West Coast &amp; Island Lifestyles</p>
          <cite>
            <a href="http://foodvib.es">http://foodvib.es</a>
          </cite>
        </div>
      </div>
      <div class="card-slot fl w-100 w-25-l w-50-m pa4">
        <div class="card">
          <a
            class="image"
            href="https://itunes.apple.com/us/app/pik/id1348172249">
            <img
              alt="Piko for iOS"
              src="static/media/piko.jpg">
          </a>
          <a
            class="title"
            href="https://itunes.apple.com/us/app/pik/id1348172249"><h3>Pik</h3></a>
          <p>Measure 3D Positions on Earth</p>
          <cite class="appstore">
            <a
              href="https://itunes.apple.com/us/app/pik%C5%8D/id1348172249?mt=8"
              title="Download on the  App Store" />
          </cite>
        </div>
      </div>
      <div class="card-slot fl w-100 w-25-l w-50-m pa4">
        <div class="card">
          <a
            class="image"
            href="https://kuokoa.life">
            <img
              alt="Kukoa.life an online shop"
              src="static/media/kuokoa-life.jpg">
          </a>
          <a
            class="title"
            href="https://kuokoa.life"><h3>kuokoa.life</h3></a>
          <p>Handcrafted &amp; Localized Goods for West Coast + Island Living</p>
          <cite>
            <a href="https://kuokoa.life">https://kuokoa.life</a>
          </cite>
        </div>
      </div>
      <div class="card-slot fl w-100 w-25-l w-50-m pa4">
        <div class="card">

          <a
            class="image"
            href="http://kuokoa.studio">
            <img
              class="scale pad"
              alt="Kuokoa Studio"
              src="static/media/kuokoa.svg">
          </a>
          <a
            class="title"
            href="http://kuokoa.studio"><h3>KUOKOA</h3></a>
          <p>Industrial Development &amp; Software Design</p>
          <cite>
            <a href="http://kuokoa.studio">http://kuokoa.studio</a>
          </cite>
        </div>
      </div>
    </div>
    <div class="section read pa4 tc">
      <h2 class="title">try read</h2>
      <div class="pv4">
        <ul>
          <li
            v-for="r in reads"
            :key="r[0]"
            class="pv1 ph2">
            <a :href="r[1]">{{ r[0] }}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import CyberSpace from './components/CyberSpace';
import Search from './components/Search';

const reads = [
  ['Hacker News', '//news.ycombinator.com'],
  ['Washington Post', '//washingtonpost.com'],
  ['Atlas Obscura', '//www.atlasobscura.com'],
  ['Nautilus', '//nautil.us'],
  ['Adbusters', '//adbusters.org'],
  ['Life & Thyme', '//lifeandthyme.com'],
  ['n+1', '//nplusonemag.com'],
  ['Civil Beat', '//civilbeat.org'],
  ['Greater Good', '//greatergood.berkeley.edu'],
  ['The Guardian', '//theguardian.com'],
  ['Timeline', '//timeline.com'],
].sort();

export default {
  name: 'Main',
  components: {
    CyberSpace,
    Search,
  },
  data() {
    return {
      reads,
      y: 0,
      text: '',
    };
  },
  created() {
    window.addEventListener('scroll', this.scrolled);
  },
  destroyed() {
    window.removeEventListener('scroll', this.scrolled);
  },
  methods: {
    scrolled() {
      this.y = window.pageYOffset;
    },
    updatedText(text) {
      this.text = text;
    },
  },
};
</script>

<style lang="scss">
@import 'main.scss';
</style>



